import React from 'react'
import QRCode from "react-qr-code";
import {Box, Button, Card, CardContent, Typography} from "@mui/material";
import {Link} from "react-router-dom";

export default function AccountSettings(props) {

    const userID = localStorage.getItem('userID')

    const style = {
        card: {
            margin: '16px',
        },
        box: {
            margin: '0 16px 0 16px',
            display: 'flex',
            justifyContent: 'flex-end'
        }
    }

    return (
        <React.Fragment>
            <Card sx={style.card}>
                <CardContent >
                    <Typography variant='subtitle1' sx={{marginBottom: '32px'}}>Az azonosítód: {userID}</Typography>
                    <QRCode value={window.location.hostname + process.env.PUBLIC_URL + '/linkAccount?userID=' + userID}/>
                </CardContent>
            </Card>
            <Box sx={style.box}>
                <Link to={'/'}><Button variant='contained' sx={{marginRight: '16px'}}>Vissza</Button></Link>
                <Link to={'/newAccount'}><Button variant='contained' onClick={props.resetApp} color='error'>Új lista</Button></Link>
            </Box>
        </React.Fragment>
    )
}