import React from 'react'
import useGetData from "../Hooks/useGetData"
import {
    Checkbox, Collapse,
    Fab,
    List,
    Link as MuiLink,
    ListItem,
    ListItemButton,
    ListItemText,
    ListSubheader,
    Typography, Box
} from "@mui/material"
import {
    Link,
} from "react-router-dom";
import AddIcon from '@mui/icons-material/Add';
import {TransitionGroup} from "react-transition-group";


export default function DisplayItems(props) {

    const [documents] = useGetData()

    const style = {
        addButton: {
            position: 'fixed',
            bottom: '16px',
            right: '16px',
            zIndex: '2',
        }
    }

    return (
        <Box sx={{overscrollBehavior: 'contain'}}>
            <List dense sx={{width: '100%', bgcolor: 'background.paper'}}>
                {[false, true].map((bool, index) => {
                    return (
                        <React.Fragment key={index}>
                            <ListSubheader
                                component='div'>{bool ? 'Már meg lett véve' : 'Még nincs megvéve'}</ListSubheader>
                            <TransitionGroup>
                                {documents.map(item => {
                                    const labelId = `checkbox-list-secondary-label-${item?.id}`
                                    if (bool === item?.item?.done) return (
                                        <Collapse key={item?.id}>
                                            <ListItem
                                                secondaryAction={
                                                    <Checkbox
                                                        edge="end"
                                                        checked={item?.item?.done}
                                                        onClick={props.handleUpdate}
                                                        id={item?.id}
                                                    />
                                                }
                                                disablePadding
                                            >
                                                <MuiLink component={Link} to={'/item/' + item.id}
                                                         sx={{width: '100%', height: '100%'}}
                                                >
                                                    <ListItemButton>
                                                        <ListItemText
                                                            id={labelId}
                                                            primary={
                                                                <Typography variant='body1'
                                                                            component='div'>{item.item.name}</Typography>
                                                            }
                                                            secondary={
                                                                <React.Fragment>
                                                                    <Typography sx={{display: 'inline'}}
                                                                                component='span'
                                                                                variant='body2'>
                                                                        {item?.item?.quantity} db
                                                                        {item?.item?.type ? ' - ' + item.item.type : ''}
                                                                        {item?.item?.shop ? ' - ' + item.item.shop : ''}
                                                                    </Typography>
                                                                </React.Fragment>
                                                            }/>
                                                    </ListItemButton>
                                                </MuiLink>
                                            </ListItem></Collapse>
                                    )
                                    return null
                                })}</TransitionGroup></React.Fragment>)
                })}
            </List>
            <Link to={'/addItem'}>
                <Fab variant='contained' color='primary' size='large'
                     sx={style.addButton}>
                    <AddIcon/>
                </Fab>
            </Link>
        </Box>
    )
}
