import React from 'react'
import useGetDocument from "../Hooks/useGetDocument";
import {Box, Button, Card, CardContent, Typography} from "@mui/material";
import {Link, useParams} from "react-router-dom";


export default function DetailedItem(props) {

    const [item] = useGetDocument()
    const {itemID} = useParams()


    const style = {
        subtitleStyle: {
            marginBottom: '4px',
        },
        buttonContainer: {
            display: 'flex',
            justifyContent: 'flex-end',
            margin: '0 16px 0 16px'
        }
    }

    return (
        <React.Fragment>
            <Card sx={{margin: '16px'}}>
                <CardContent>
                    <Typography variant='h4' sx={{marginBottom: '16px'}}>{item.name}</Typography>
                    <Typography variant='subtitle1'>
                        <div style={style.subtitleStyle}>{'Darab: ' + item?.quantity}</div>
                        <div style={style.subtitleStyle}>{'Típus: ' + (item?.type ? item.type : '-')}</div>
                        <div style={style.subtitleStyle}>{'Bolt: ' + (item?.shop ? item.shop : '-')}</div>
                        <div style={style.subtitleStyle}>
                            <b>{item?.done ? 'Már meg lett véve!' : 'Még nem lett megvéve!'}</b>
                        </div>
                    </Typography>
                </CardContent>
            </Card>
            <Box style={style.buttonContainer}>
                <Link to={'/'}><Button variant='contained' sx={{marginRight: '16px'}}>Vissza</Button></Link>
                <Link to={'/'}><Button variant='contained' color='error' onClick={() => {
                    props.deleteItem(itemID)
                }}>Törlés</Button></Link>
            </Box>
        </React.Fragment>
    )
}