import React, {useState} from 'react'
import {Box, Button, Card, CardContent, Grid, TextField, Typography} from "@mui/material";
import {Link} from "react-router-dom"
import GenerateRandomCode from 'react-random-code-generator';

export default function NewAccount(props) {

    const [userID, setUsedID] = useState('')

    const GenerateUserID = () => {
        props.setUserID(GenerateRandomCode.TextNumCode(8, 8))
    }

    const submitUserID = () => {
        props.setUserID(userID)
    }

    const style = {
        card: {
            margin: '16px',
        },
        content: {
            height: '100%',
        }
    }

    return (
        <React.Fragment>
            {
                <Box>
                    <Card sx={style.card}>
                        <CardContent sx={style.content}>
                            <Grid container rowSpacing={3} alignItems='center' justifyContent='space-between'
                                  direction='column'>
                                <Grid item>
                                    <Link to='/'>
                                        <Button variant='contained'
                                                onClick={GenerateUserID}
                                        >
                                            Új lista létrehozása</Button></Link>
                                </Grid>
                                <Grid item>
                                    <Typography variant='subtitle1'>Vagy</Typography>
                                </Grid>
                                <Grid item>
                                    <Typography variant='subtitle1'>Meglévő lista megnyitása:</Typography>
                                    <TextField sx={{width: '100%'}}
                                               onChange={event => setUsedID(event.target.value)}/>
                                    <Link to={{
                                        pathname: '/linkAccount',
                                        search: `?userID=${userID}`
                                    }}><Button onClick={submitUserID}>Kód használata</Button></Link>
                                </Grid>
                            </Grid>
                        </CardContent>
                    </Card>
                </Box>
            }
        </React.Fragment>
    )
}