import React from 'react';
import {useState} from "react";
import {Box, Button, Card, InputAdornment, TextField} from "@mui/material";
import {Link} from "react-router-dom";

export default function AddItem(props) {

    const [name, setName] = useState('')
    const [quantity, setQuantity] = useState(1)
    const [type, setType] = useState('')
    const [shop, setShop] = useState('')

    const createItem = () => {
        return {
            name,
            quantity,
            type,
            shop,
            user: localStorage.getItem('userID'),
            done: false,

        }
    }

    const style = {
        textField: {
            width: '100%',
            marginBottom: '16px',
        },
        card: {
            padding: '16px',
            margin: '16px',
        },
        buttonContainer: {
            display: 'flex',
            margin: '0px 16px 0px 16px',
            justifyContent: 'flex-end'
        }
    }

    return (
        <Box component='form' autoComplete='off'>
            <Card sx={style.card}>
                <TextField sx={style.textField} label='Product name' onChange={event => setName(event.target.value)}
                           required/>
                <TextField
                    sx={style.textField}
                    label='Quantity'
                    defaultValue={1}
                    type='number'
                    InputProps={{
                        endAdornment: <InputAdornment position="end">db</InputAdornment>
                    }}
                    onChange={event => setQuantity(Number(event.target.value))}/>
                <TextField sx={style.textField} label='Type' onChange={event => setType(event.target.value)}/>
                <TextField sx={style.textField} label='Shop' onChange={event => setShop(event.target.value)}/>
            </Card>
            <Box sx={style.buttonContainer}>
                <Link to={'/'}><Button variant='contained' color='primary' sx={{marginRight: '16px'}}>Elvet</Button></Link>
                <Link to={'/'}><Button variant='contained' color='success' onClick={() => {
                    props.addItem(createItem())
                }} type='submit'>Hozzáad</Button></Link>
            </Box>
        </Box>
    );
};